import React, { Component } from 'react';
import HamburgerMenu from 'react-hamburger-menu';
import { Link } from 'react-router-dom';
import Media from 'react-media';
import Navs from './navs/navs';
import style from './startScreen.module.css';
import logo from '../../../img/logo/LogoCCT.png';
import logoW from '../../../img/logo/LogoCCT.webp';
import buttonsIMG from '../../../img/elements/buttonsGroup.svg';
import imgPortal from '../../../img/infoImg/portal.png';
import imgPortalW from '../../../img/infoImg/portal.webp';

export default class StartScreen extends Component {
  state = {
    open: false,
  };

  handleClick = () => {
    this.setState(prevState => ({ open: !prevState.open }));
  };

  render() {
    const { open } = this.state;
    return (
      <div className={style.wrapper}>
        <div className={style.navBar}>
          <div className={style.navBarNotTransparent}>
            <Link to="/">
              <picture>
                <source srcSet={logoW} type="image/webp" />
                <source srcSet={logo} type="image/png" />
                <img className={style.img} src={logo} alt="Logo" />
              </picture>
            </Link>
            <div className={style.mediaMenu}>
              <Media query={{ maxWidth: 1200 }}>
                {matches =>
                  !matches && <span className={style.span}>МЕНЮ</span>
                }
              </Media>
              <HamburgerMenu
                isOpen={open}
                menuClicked={this.handleClick}
                color="#c0c0c2"
              />
            </div>
          </div>
        </div>
        <h2 className={style.header}>
          <Media query={{ maxWidth: 1200 }}>
            {matches =>
              !matches && (
                <img
                  className={style.buttons}
                  src={buttonsIMG}
                  alt="decorate"
                />
              )
            }
          </Media>
          коммунальный портал
        </h2>
        <p className={style.text}>
          Официальный имиджевый сайт, имеющий ряд ключевых функций,
          <Media query={{ maxWidth: 1200 }}>
            {matches => !matches && <br />}
          </Media>
          необходимых для коммунального предприятия
        </p>
        <picture>
          <source srcSet={imgPortalW} type="image/webp" />
          <source srcSet={imgPortal} type="image/png" />
          <img className={style.imgPortal} src={imgPortal} alt="Portal" />
        </picture>
        <Navs open={open} />
      </div>
    );
  }
}
